<template>
  <section class="container">
    <div class="login-container">
      <h3 class="title">账号登录</h3>
      <van-field
        left-icon="phone-o"
        v-model="mobile"
        clearable
        placeholder="请输入用户名"
      />
      <van-field
        left-icon="records"
        v-model="password"
        :type="type"
        clearable
        placeholder="请输入密码"
        :right-icon="rightIcon"
        @click-right-icon="isPassword = !isPassword;"
      />
      <van-button
        class="login"
        slot="button"
        size="small"
        type="primary"
        @click="login"
        >登录</van-button
      >
      <div class="buttons">
        <van-button slot="button" size="small" type="warning"  @click="normalLogin"
          >直接进入</van-button
        >
        <van-button slot="button" size="small" type="primary" @click="register"
          >用户注册</van-button
        >
        <van-button slot="button" size="small" type="default" @click="findPassword"
          >找回密码</van-button
        >
      </div>
    </div>
  </section>
</template>
<script>
export default {
  computed: {
    type() {
      return this.isPassword ? 'password' : 'text';
    },
    rightIcon() {
      return this.isPassword ? 'closed-eye' : 'eye';
    }
  },
  data() {
    return {
      // 账户名
      mobile: '',
      // 密码
      password: '',
      // 切换密码展示
      isPassword: true
    };
  },
  methods: {
    async normalLogin() {
      if (!this.mobile) {
        this.$toast({
          text: '请填写手机号'
        })
        return;
      }
      if (!this.password) {
        this.$toast({
          text: '请填写密码'
        })
        return;
      }
      // todo form validate
      await this.$store.dispatch('NORMAL_LOGIN', {
        mobile: this.mobile,
        password: this.password
      });
    },
    async login() {
      if (!this.mobile) {
        this.$toast({
          text: '请填写手机号'
        })
        return;
      }
      if (!this.password) {
        this.$toast({
          text: '请填写密码'
        })
        return;
      }
      await this.$store.dispatch('LOGIN', {
        mobile: this.mobile,
        password: this.password
      });
    },
    register() {
      this.$router.push({
        name: 'signup'
      })
    },
    findPassword() {
      this.$router.push({
        name: 'findPassword'
      })
    }
  }
};
</script>
